import React from 'react';
import {Grid} from "@material-ui/core";
import TabSideNavApplicationForm from "../application-form/TabSideNavApplicationForm";
import TabPanelSideNavApplicationForm from "../application-form/TabPanelSideNavApplicationForm";
import {UserStore} from "../../stores";

interface IViewApplicationForm {
    loading: boolean,
    userStore: UserStore | null | undefined,
}

const ViewApplicationForm: React.FC<IViewApplicationForm> = ({loading = true, userStore}) => {
    const [value, setValue] = React.useState(1);
    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    if (loading) return <></>;

    return (<div>
        <Grid container>
            <Grid item xs={4} sm={2}>
                <TabSideNavApplicationForm
                    value={value}
                    userStore={userStore}
                    onChange={handleChange}
                    showConfirmation={false}
                />
            </Grid>
            <Grid item xs={8} sm={10}>
                <TabPanelSideNavApplicationForm
                    value={value}
                    userStore={userStore}
                />
            </Grid>
        </Grid>
    </div>);
};

export default ViewApplicationForm;