import React, {useEffect, useState} from "react";
import ContentLayout from "../../components/commons/ContentLayout";
import {Box, Divider, Grid, Tab, Tabs, Typography, useTheme} from "@material-ui/core";
import {FirebaseServices} from "../../services";
import {useParams} from "react-router";
import LoadingBackground from "../../components/commons/LoadingBackdrop";
import ViewApplicationForm from "../../components/admin/ViewApplicationForm";
import SwipeableViews from 'react-swipeable-views';
import {UserStore} from "../../stores";

function a11yProps(index: any) {
    return {
        id: `wrapped-tab-${index}`,
        'aria-controls': `wrapped-tabpanel-${index}`,
    };
}

interface TabPanelProps {
    children?: React.ReactNode;
    index: any;
    value: any;
}

function TabPanel(props: TabPanelProps) {
    const {children, value, index, ...other} = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`wrapped-tabpanel-${index}`}
            aria-labelledby={`wrapped-tab-${index}`}
            {...other}
        >
            {value === index && <Box p={3}>{children}</Box>}
        </Typography>
    );
}

interface IDetailUser {
    uidProps?: string,
    hiddenTitle?: boolean,
}

const DetailUser: React.FC<IDetailUser> = ({
                                               uidProps,
                                               hiddenTitle = false,
                                           }) => {
    const theme = useTheme();
    let {uid} = useParams();
    const [value, setValue] = React.useState<number>(0);
    const [loading, setLoading] = useState<boolean>(false);
    const [userStore] = useState<UserStore>(new UserStore());

    useEffect(() => {
        const init = async () => {
            setLoading(true);

            const resApplicationForm = await FirebaseServices.getInstance().fetchApplicationForm(uidProps ?? uid);
            userStore.setApplicationForm(resApplicationForm);

            setLoading(false);
        };

        init();
        // eslint-disable-next-line
    }, [uid]);

    const handleChange = (event: React.ChangeEvent<{}>, newValue: number) => {
        setValue(newValue);
    };

    const handleChangeIndex = (index: number) => {
        setValue(index);
    };

    if ((uidProps === undefined || uidProps === null) &&
        (uid === undefined || uid === null))
        return <></>;

    return (<ContentLayout>
        <LoadingBackground open={loading}/>
        <Grid item xs={12}>
            {
                hiddenTitle ? <></> : <>
                    <Typography variant="h3">
                        Applicant Detail Data
                    </Typography>
                    <Divider/>
                    <br/>
                </>
            }
            <Tabs
                value={value}
                onChange={handleChange}
                variant="fullWidth"
                centered
                aria-label="Detail users tab">
                <Tab value={0} label="Application Form" {...a11yProps('one')} />
                <Tab value={1} label="Letter of Commitment" {...a11yProps('two')} />
                <Tab value={2} label="Travel Documents" {...a11yProps('three')} />
            </Tabs>
            <SwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={value}
                onChangeIndex={handleChangeIndex}
            >
                <TabPanel value={value} index={0}>
                    <ViewApplicationForm userStore={userStore} loading={loading}/>
                </TabPanel>
                <TabPanel value={value} index={1}>
                    Coming soon <span role={'img'} aria-label={'Ganbantte'}></span>
                </TabPanel>
                <TabPanel value={value} index={2}>
                    Coming soon <span role={'img'} aria-label={'Ganbantte'}></span>
                </TabPanel>
            </SwipeableViews>
        </Grid>
    </ContentLayout>);
};

export default DetailUser;
